    font-size: 16px; }
  .container-form ::-moz-placeholder {
    color: white;
    opacity: 0.5;
    font-family: 'Special Elite', cursive;
    font-size: 16px; }
  .container-form :-ms-input-placeholder {
    color: white;
    opacity: 0.5;
    font-family: 'Special Elite', cursive;
    font-size: 16px; }
  .container-form ::-ms-input-placeholder {
    color: white;
    opacity: 0.5;
    font-family: 'Special Elite', cursive;
    font-size: 16px; }
  .container-form ::placeholder {
    color: white;
    opacity: 0.5;
    font-family: 'Special Elite', cursive;
    font-size: 16px; }
  .container-form img {
    position: absolute;
    top: 0;
    left: 0; }

.btn-2 {
  letter-spacing: 0; }

.btn-2:hover,
.btn-2:active {
  letter-spacing: 2px; }

.btn-2:after,
.btn-2:before {
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  border: 1px solid rgba(255, 255, 255, 0);
  bottom: 0px;
  content: " ";
  display: block;
  margin: 0 auto;
  position: relative;
  -webkit-transition: all 280ms ease-in-out;
  transition: all 280ms ease-in-out;
  width: 0; }

.btn-2:hover:after,
.btn-2:hover:before {
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  border-color: #fff;
  -webkit-transition: width 350ms ease-in-out;
  transition: width 350ms ease-in-out;
  width: 90%; }

.btn-2:hover:before {
  bottom: auto;
  top: 0;
  width: 90%; }

.container-results {
  width: 100%;
